/*Base styling for basic app containers*/
.container {
    height: calc(100vh - 200px);
    width: 100vw;
}

.app-container {
    position: absolute;
    height: calc(100vh - 40px);
    width: 100vw;
}

.side-bar-container {
    left: 180px;
    top:140px;
    width: calc(100vw - 180px);
    height: calc(100vh - 140px);
    overflow: scroll;
}

.flex-half-n-half {
    display: flex;
    grid-template-columns: 50% 50%;
}

/*Fixed position container*/
.float-container {
    position: fixed;
    top:0px;
    left:0px;
    height: 100vh;
    width: 100vw;
    z-index: 100;
    background-color: rgba(0, 0, 0, 0.5);
    display: grid;
    align-content: center;
    justify-content: center;
}