#login-box{
    position: absolute;
    max-width: 40vw;
    min-width: 40vw;
    min-height: 60vh;
    max-height: 60vh;
    left: 30vw;
    right: 30vw;
    top: 20vh;
    bottom: 20vh;
    align-content: center;
}
#login-info{
    padding: 5px;
    display: grid;
    row-gap: 10px;
    justify-items: center;
    height: 52.5vh;
}
#login-form {
    display: grid;
    padding: 25px;
    gap: 25px;
    height: 15vh;
    position: absolute;
    bottom: 130px;
}
#login-form>.form-info-item {
    display: flex;
    gap: 25px;
    height: 30px;
}
#login-form>.form-info-item>div {
    font-weight: 550;
    text-align: left;
    line-height: 30px;
}

#login-form>.form-info-item>input {
    height: 30px;
}

.login-logo {
    position: absolute;
    top:100px;
    height: 150px;
    width: 150px;
}

.action-button.login-submit {
    position: absolute;
    bottom: 30px;
    border-radius: 5px;
    width: 10em;
    height: 3em;
    line-height: 3em;
    font-weight: 700;
    background-color: var(--basic-button-bg);
    color: var(--basic-button-color);
    filter: var(--drop-shadow)
}

.action-button.login-submit:hover {
    background-color: var(--basic-button-border);
    color: var(--basic-button-hover-text)
}

.login-username {
    text-transform: uppercase;
}