/*Customization for dropdowns*/
.dropdown-option {
    padding: 5px;
    cursor: pointer;
}
.dropdown-arrow {
    width: 10px;
    position: relative;
    left: 40%;
}

.dropdown-option:hover {
    background-color: rgb(141, 62, 62);
}
.dropdown-option.selected.default {
    color: rgb(120,120,120)
}

.dropdown-option.selected {
    background-color: var(--card-base-light);
    outline: 1px solid #ccc;
    border-radius: 5px;
    width: 150px;
    z-index: 20;
    position: relative;
    color: rgb(0,0,0)
}

.dropdown-option:hover {
    background-color: #ccc;
}

.dropdown-list {
    padding-top: 5px;
    padding-bottom: 5px;
    width: 100%;
    background-color: var(--card-base-light);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border: 1px solid #ccc;
    position: absolute;
    margin-left: -5px;
    margin-top: 5px;
}

.dropdown-list>.Search-Bar {
    max-width: 125px;
}