/*App Map customization*/

.app-header {
    left: 180px;
    top:60px;
    position: absolute;
    display: grid;
    gap:2.5px;
}

.app-header>.action-button {
    background-color: #e5e9f0;
    border: 1.25px solid #b2bac7;
    display: flex;
    align-items: center;
    gap: 7.5px;
}

.app-header>.action-button:hover {
    background-color: #b2bac7;
    cursor: pointer;
}

.app-header>.action-button>div {
    color:rgb(25,25,25)
}

.app-header>.action-button>img {
    height: 17.5px;
    width: 17.5px;
    margin-left: 5px;
}

.app-path {
    text-align: left;
    font-size: 40px;
}

.app-select {
    background-color: #fff;
    width: 97.5%;
    height: 100px;
    gap: 25px;
    border: 1px solid #e5e5e5;
    border-radius: 5px;
    display: grid;
    grid-template-columns: 80% 6% 6%;
}

.app-info {
    height: 100%;
    display: grid;
    grid-template-rows: 60% 40%;
    margin-left: 2vw;
}

.app-info-title {
    font-size: 30px;
    font-weight: 550;
    text-align: left;
    line-height: 80px;
}

.app-info-descr {
    font-style: italic;
    text-align: left;
}


.app-list {
    display: grid;
    gap:25px;
}

.app-section {
    margin-bottom: 50px;
}

.app-section-header {
    text-align: left;
    margin-top: 5px;
    margin-bottom: 5px;
    font-weight: 600;
