#admin-JSON-viewer {
    margin-top: 10px;
    max-width: calc(97.5vw - 180px) !important;
    min-width: calc(97.5vw - 180px) !important;
    height: calc(95vh - 140px);
    overflow: scroll;
}

.card-content>.tablerow {
    margin: 5px;
    border-radius: 10px;
}


.card-content>.tablerow:hover {
    background-color: #ededed;
}

/*JSON Table Configurations*/
#users {
    min-width: 90vw;
    min-height: 70vh;
    max-height: 70vh;
}
#users-card-content>.card-content {
    height: 60vh;
    overflow: scroll;
}
#users-card-content>.card-content>.tablerow {
    grid-template-columns: 6% 14% 14% 8% 8% 14% 20% 20% !important;
    height: 75px;
}

#wojournal_currmonth {
    max-width: 90vw;
    min-height: 70vh;
    max-height: 70vh;
}
#wojournal_currmonth-card-content>.card-content {
    height: 65vh;
    overflow: scroll;
}
#wojournal_currmonth-card-content>.card-content>.tablerow {
    grid-template-columns: 9% 5% 1% 5% 5% 5% 5% 5% 5% 5% 0.5% 6% 6% 6% 1% 10% 6% 6% 10% !important;
    height: 150px;
    margin: 5px;
    border-radius: 10px;
}

#wojournal_currmonth-card-content>.tablerow>:first-child::before {
    content: "$";
}

#wojournal_currmonth-card-content>.tablerow:hover {
    background-color: #ededed;
}